import React, { useEffect, useState } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col } from 'antd';
import moment from 'moment';
import FormSearch from './FormSearch';
import DataSheet from './DataSheet';
import { getGetHolidayByYear } from '../../../services/holiday';
import { getStudySessions } from '../../../services/studySession';

const getMonthRange = (startDate, endDate, format = 'YYYY-MM') => {
  const result = [];
  if (endDate.isBefore(startDate)) {
    console.error('End date must be greated than start date.');
  }
  while (startDate.isBefore(endDate)) {
    result.push(startDate.format(format));
    startDate.add(1, 'month');
  }
  result.push(endDate.format(format));
  return [...new Set(result)];
};

const getWeekRange = (startDate, endDate) => {
  const result = [];
  let currentNoWeek = 0;
  let startDateCheck = startDate.clone();
  let i = 0;
  if (startDateCheck.day() === 0) {
    // if = Sunday
    startDateCheck = startDateCheck.add(1, 'day');
  }
  while (startDateCheck.isBefore(endDate) && i < 100) {
    const dayOfWeek = startDateCheck.day();
    const endWeekCurrentCheck = startDateCheck.clone().add(7 - dayOfWeek, 'day');
    result.push({
      noWeek: currentNoWeek + 1,
      month: startDateCheck.clone().format('MM/YYYY'),
      fromDate: startDateCheck.clone().format('DD/MM/YYYY'),
      toDate: endWeekCurrentCheck.clone().format('DD/MM/YYYY'),
    });
    currentNoWeek += 1;
    startDateCheck = endWeekCurrentCheck.clone().add(1, 'day');
    i += 1;
  }
  return result;
};

const HolidayYearPage = () => {
  const [studySessions, setStudySessions] = useState([]);
  const [yearEduSelected, setYearEduSelected] = useState([]);
  const [monthEdus, setMonthEdus] = useState([]);
  const [weekEdus, setWeekEdusEdus] = useState([]);
  const callGetStudySessions = () => {
    const { data } = getStudySessions();
    setStudySessions(data);
  };
  useEffect(() => {
    callGetStudySessions();
  }, []);

  useEffect(() => {
    const fromDate = moment(yearEduSelected.fromDate);
    const toDate = moment(yearEduSelected.toDate);
    setMonthEdus(getMonthRange(fromDate.clone(), toDate.clone(), 'MM/YYYY'));
    setWeekEdusEdus(getWeekRange(fromDate.clone(), toDate.clone()));
  }, [yearEduSelected]);

  const handleSubmitFormSearch = values => {
    setYearEduSelected(values.objSelected.yearEduId);
    getGetHolidayByYear(values);
  };
  console.log(studySessions, yearEduSelected, monthEdus, weekEdus);
  return (
    <Card title="Ngày nghỉ trong năm">
      <FormSearch handleSubmit={handleSubmitFormSearch} />
      <Row>
        <Col span={24}>
          <DataSheet />
        </Col>
      </Row>
    </Card>
  );
};

export default props => (
  <Page {...props}>
    <HolidayYearPage />
  </Page>
);



