import { request } from 'utils';

const prefix = '/api/education';

export const getGetHolidayByYear = ({
  yearEduId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/Holiday/GetHolidayByYear',
    method: 'GET',
    params: { yearEduId, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });



